@import "../global/mixins";
@import "../global/variables";

.hero-wrapper {
  position: relative;
  width: 100%;
  max-width: $max-desk-w;
  height: 80vh;
  min-height: 740px;
  // background-color: $purple;
  border-radius: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  // overflow: hidden;

  .bg-blur-ball-right {
    width: 500px;
    height: 500px;
    border-radius: 50%;
    background-color: $purple;
    opacity: 0.1;
    filter: blur(100px);
    position: absolute;
    bottom: 0;
    right: 0;
  }

  .bg-blur-ball-left {
    width: 500px;
    height: 500px;
    border-radius: 50%;
    background-color: $accent;
    opacity: 0.1;
    filter: blur(100px);
    position: absolute;
    top: 0;
    left: 0;
  }

  .hero-content {
    width: 100%;
    max-width: $max-desk-w;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    // padding: 42px;
    z-index: 4;

    .heading {
      width: 100%;
      max-width: 1000px;
      .hero-heading,
      .hero-heading span {
        font-family: $secondfont;
        font-size: 68px;
        font-weight: 400;
        letter-spacing: -3px;
        line-height: 72px;
        text-align: center;
      }
      .hero-heading {
        color: $light-text;

        span {
          color: $accent;
        }
      }

      .btn-container {
        margin-top: 36px;
        display: flex;
        justify-content: center;
        gap: 8px;

        .prim-btn {
          opacity: 0;
          height: 60px;
          width: 200px;
          scale: 0;
          border: none;
          border-radius: 200px;
          color: $nearblack;
          background-color: $accent;
          font-size: 18px;
          font-weight: 500;
          transition: opacity 0.3s ease, scale 0.3s ease;
        }

        .prim-btn.visible:hover {
          scale: 1.15;
          cursor: pointer;
        }

        .prim-btn.visible {
          opacity: 1;
          scale: 1;
        }

        .prim-btn.hidden {
          opacity: 0;
          scale: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 892px) {
  .hero-wrapper {
    height: 80vh;

    .hero-content {
      // padding: 24px;
      .heading {
        padding: 16px;
        .hero-heading,
        .hero-heading span {
          font-size: 10vmin;
          line-height: 104%;
        }
      }
    }
  }
}

@media screen and (max-width: 664px) {
  .hero-wrapper {
    justify-content: flex-start;
    .hero-content {
      justify-content: flex-start;
      .heading {
        .hero-heading {
          text-align: start;
        }
        .btn-container {
          justify-content: flex-start;
        }
      }
    }
    .bg-blur-ball-left,
    .bg-blur-ball-right {
      width: 300px;
      height: 300px;
    }
  }
}
